@import url('https://fonts.googleapis.com/css2?family=Agdasima:wght@700&family=Josefin+Sans:wght@300;400;600&family=Mulish:wght@300&family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap');
*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}
/* font-family: 'Poppins', sans-serif; */
/* font-family: 'Playfair Display', serif; */
.navbar{
  font-family: 'Poppins', sans-serif;
  font-size: 17px;
  line-height: 17px;
  text-transform: uppercase;
  box-shadow: 0 8px 6px -6px gray;
  border-bottom: 0.4px solid gray;
}
.nav-link{
  font-weight: 300px !important;
}
.active{
  border-bottom: 1px solid black;
  margin: 1px;
}
.nav-link:hover{
  border-bottom: 3px solid lightgrey;
  color: lightgrey;
  
}

/* ---------Footer Section---------- */
.footer{
  color:white;
  padding: 5px;
  background: gray;
}
.footer a{
  text-decoration: none;
  color: white;
  padding: 10px;
}
.footer a:hover{
  color:lightgray;
  border-bottom: 2px solid lightgray;
}
/* ****cart section***** */
.head{
  text-transform: uppercase;
}
/* -------------Page Not Found Section --------------- */
.pnf{
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.pnf_tittle{
  font-size: 100px;
  font-weight: 700px;
}
.pnf_heading{
  font-weight: normal;
}
.pnf-btn {
  color: black;
  border: 1px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}
.pnf-btn:hover{
  background-color: gray;
  color: white;
}

/* -------------Register Page--------- */
.register{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 70vh;
  flex-direction: column;

}
.card img{
  height: 350px;
  padding: 5px;
}

.product_link{
  text-decoration: none !important;
  color: black !important;
}

/* *************About Section*************** */
/* ************************************** */

#header{
  width: 100%;
  height: 80vh;
  margin-top: 70px;
}
#header h1{
  margin: 0 0 10px 0;
  font-size: 48px;
  font-weight: 700;
  color: black;
}
#header .brand-name{
  color: #3498db;
}
#header h2{
  color: #484848;
  font-size: 24px;
}

.header-img{
  text-align: right;
}
#header .animated {
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes up-down {
  0% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY( -10px );
  }
}
@keyframes up-down {
  0% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(-10px);
  }
}

/* Card  Section */
.card{
  width: 100%;
  height: auto;
  
}
