.card:hover{
  box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 0.1);
  transform: scale(1.10, 1.10);
}
.card_img{
  width: 50%;
  height: 235px;
}

.card-title{
  text-transform: uppercase;
}
.card-text{
  margin-bottom: 8px;
  padding: 0;
  text-align: left;
}
